
/* HEADER */

.header-container{
    /* border: 2px solid black; */
    border-bottom: 2px solid black;
    margin: 1rem 1rem 1rem 1rem ;
    /* border-radius: 10px; */
    display: flex;
    flex-flow: row;
    justify-content: space-between;
    padding: 0px 20px 2px 20px;
    box-sizing: border-box;
    height: 12vh;
}

.header-container ul {
    display: flex;
    flex-flow: row;
    list-style: none;
}

.header-container ul li {
    margin: 0.5rem 1rem 1rem 1rem;
    font-size: xx-large;
    padding: 1rem;
    
}
.header-container ul li:hover {
    /* border-radius: 5px;
    box-sizing: border-box;
    height: 4rem;
    width: 15rem;
    padding: 0px 0px 0px 0px; */
    transform: scale(1.1);
   cursor: default;
    border-radius: 5px;
    background-color: rgb(67, 67, 67);
    color: rgb(240, 241, 210);
    
}

.home-name h1 {
    /* border : 1px solid black; */

    border-radius: 5px;
    box-sizing: border-box;
    padding: 5px 0px 0px 0px;
    width: 20rem;
    height: 4rem;
    background-color: rgb(67, 67, 67);
    color: rgb(240, 241, 210);
}

.home-name h1:hover {
   transform: scale(1.02);
   cursor: default;
}

/* About */


.about-container{
    display: flex;
    flex-flow: row ;
}

.about-details{
    align-self: auto;
    border-radius: 5px;
    justify-items: left;
    border-top-left-radius: 100px;
    border-bottom-left-radius:100px ;
    margin: auto auto;
    background-color: rgb(67, 67, 67);
    width:66%;
    height: 66vh;
    display: flex;
    flex-flow: row;
}
.about-details p{
   font-size: 5ch ;
   color: white;
   font-family: Georgia, 'Times New Roman', Times, serif;
   padding: 0px 0px 0px 0px;
   
}
.about-details p span{
    border-bottom: 3px solid rgb(240, 241, 210) ;
    font-size: 2.5ch ;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
 }

 .about-text{
    border: 2px solid black;
    /* margin-left:-20rem ; */
    display: flex;
    flex-flow: column;
    justify-content: left;
    width: 70%;
    height: 100%;
}
.about-graphics{
    border: 2px solid black;
    align-self: auto;
    margin: auto auto;

    width:30%;
    height: 66vh;
}
.about-barcode{
    
    /* border: 2px solid black; */
    width: 30%;
    height: 100%;
   
}

.barcode{
    font-family: 'Libre Barcode 128', cursive;
    font-size:22ch;
   
   
}



.barcode-container{
    /* border: 2px solid black; */
    transform: rotate(270deg);
    width: 30vw;
    height: 15vh;
    margin:16rem 1rem 1rem -5rem;
    
}



/* Footer */
.footer-container{
    height: 10vh;
}

